nav{
    min-height:50px;
    width:100%;
    margin:0% auto;
    border-top: solid 3px #ef8236;
    box-shadow: 0px 1px 3px #00000033;
    position:-webkit-sticky;
    z-index: 5;
    top:0%;
    left:0%;
    background-color: #f8f9f9;
    display:flex;
    justify-content:centent;
    align-items:center;
    
}
.navbar{
    height:100%;
    min-width: 85%;
    display:flex;
    align-items:center;
    justify-content: space-between;
}
.nav-logo{
    padding:5px 25px;
}
.nav-items{
    margin: 0px 3px;
    font-size: small;
    font-weight: 500;
    text-decoration: none;
    color:rgb(69,69,69)
   
}
.nav-btn{
    cursor:pointer;
    border-radius: 20px;
    padding:10px 20px;
}
.nav-item:hover{
    background-color: rgb(255,255,255);
}
.navbar form{
    flex-grow{
        padding:0px 13px;
        position:relative;
    }
}
.navbar form input{
    min-width:90%;
    margin: 0;
    padding:8px 10px 8px 32px;
    font-size:13px;
    border:solid 1 px #0000003e;
    border-radius:3px;
}
.navbar form .search-icon{
    position:absolute;
    left:25px;
    top:8px;

}
.nav-links{
    padding:7px 13px;
    border:solid 1px blue;
    border-radius: 3px;
    background-color:#e7f8fe;
    cursor:pointer;
}
.nav-links:hover{
    background-color:#d3e4eb;
}
.main-nav{
    min-height:50px;
    width:100%;
}